<template>
  <div :style="{'width':width}">
    <el-form ref="formData" v-model="formData" label-position="top" style="width:100%;text-align: left;">
      <el-form-item label="请输入地下城体力家具数量:">
        <el-input v-model="formData.underNum" type="number" :max="3" :min="0">
        </el-input>
      </el-form-item>
      <el-form-item label="请输入工会体力桌等级:">
        <el-input v-model="formData.houseLevel" type="number" :max="9" :min="1">
        </el-input>
      </el-form-item>
      <!-- <el-form-item label="请输入现在人物等级:">
        <el-input v-model="formData.charLevel" type="number" max="85" min="30" >
        </el-input>
      </el-form-item> -->
      <!-- <el-form-item label="请输入现有体力:">
        <el-input v-model="formData.tangoNow" type="number" max="143" min="88" >
        </el-input>
      </el-form-item> -->
      <el-form-item label="请选择下线时间:">
        <el-date-picker v-model="formData.offTime" type="datetime" placeholder="选择日期时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="请选择上线时间:">
        <el-date-picker v-model="formData.onTime" type="datetime" placeholder="选择日期时间">
        </el-date-picker>
      </el-form-item>
      <el-form-item v-if='formData.info' style="text-align: left;" v-html="formData.info">
      </el-form-item>
      <el-form-item style="text-align: right;">
        <el-button @click='calculate' type='primary'>
          计算
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  // const TOP = 999
  // const BUY = 120
  // const DAILY = {
  //   first: {
  //     start: 12,
  //     unit: 100,
  //     end: 29,
  //     emailbale: false
  //   },
  //   last: {
  //     start: 18,
  //     unit: 100,
  //     end: 29,
  //     emailbale: false
  //   }
  // }
  const DIANZAN = 10
  const UNDER = {
    unit: 12,
    size: 360,
    max: 48,
    emailbale: true
  }
  // const MINLEVEL = 30
  // const MINHOLD = 88
  const AUTO = {
    unit: 1,
    size: 6
  }
  export default {
    name: 'Tango',
    data() {
      return {
        formData: {
          houseLevel: 6,
          underNum: 3,
          charLevel: 50,
          offTime: new Date(2020, 3, 30, 23, 0, 0),
          onTime: new Date(2020, 4, 1, 8, 0, 0),
          ifLevelUp: false,
          info: ''
        },
        width: '400px'
      }
    },
    created() {
      if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
        this.width = '100%'
      }
    },
    computed: {
      table() {
        let unit = 22 + this.formData.houseLevel * 2
        return {
          unit,
          max: unit * 24,
          size: 360,
          emailbale: true
        }
      }
    },
    methods: {
      calculateAuto(minutes) {
        return AUTO.unit * parseInt(minutes / AUTO.size)
      },
      calculateHouse(minutes) {
        let times = parseInt(minutes / UNDER.size)
        times = times >= 4 ? 4 : times
        return UNDER.unit * times * this.formData.underNum + this.table.unit * times
      },
      getMinutes(date) {
        return date.getTime() / 60000
      },
      getDateString(date, offset) {
        let year = date.getFullYear()
        let mon = date.getMonth()+1
        let day = date.getDate()
        let hour = date.getHours()
        let min = date.getMinutes()
        let sec = date.getSeconds()
        if (offset) {
          date = new Date(year, mon - 1, day + offset, hour, min, sec)
          return this.getDateString(date)
        }
        function formTwo(str) {
          return str > 9 ? str : '0' + str
        }
        return `${formTwo(mon)}月${formTwo(day)}日${formTwo(hour)}:${formTwo(min)}`
      },
      calculate() {
        let minutes = this.getMinutes(this.formData.onTime) - this.getMinutes(this.formData.offTime)
        console.log(minutes)
        if(minutes<=0){
          alert('日期错误')
          return
        }
        // let autoTango = this.calculateAuto(minutes)
        let houseTango = this.calculateHouse(minutes)
        let totalhouseTango = this.calculateHouse(1440)
        // let dailyTango = DAILY.first.unit + DAILY.first.unit
        this.formData.info = `<p>建议：</p><ul style='line-height:30px'>
          <li>${this.getDateString(this.formData.offTime, -1)} 领完工会体力，已经过时则不领取，会少一部分体力。</li>
          <li>${this.getDateString(this.formData.offTime)} 前清完每日并调整体力至79（请确保能在跳80之前完成购买和领取操作）。</li>
          <li>购买6管体力，领取任务体力200，体力到达999</li>
          <li>领取工会体力${totalhouseTango},行会点赞${DIANZAN}体力。</li>
          <li>${totalhouseTango + DIANZAN}体力会存入邮件</li>
          <li>${this.getDateString(this.formData.onTime)} 上线时领取工会体力，总共获得${totalhouseTango + DIANZAN + houseTango + 999}体力</li>
          </ul>`
      },
    }
  }
</script>